	module.exports = {"data":{"markdownRemark":{"html":"<p>It was a truly enlightening trip to Denmark, attending Design Matters 2018 in Copenhagen (Sep 26 -27, 2018). Not only did the talks and workshops given by design practitioners across the industry are illuminating, it had also made me realised to take a step back to reflect on the whole workflow of design process.</p>\n<h2>Design for feelings</h2>\n<p>One of the recurring theme during this year conference, is the re-discovery of emotional design. Few speakers’ keynote coincidentally brought up the importance of expressive design, while utilitarian and minimal design has been dominated by industry leaders like Apple and Google. Tobias Ahlin from Minecraft stressed on the building emotionally engaging design style, that would eventually craft a distinctive voice for the brand. </p>\n<p>The question, “What are the key moments how your user feel” should also be addressed during the design process, aside from the quantitive approach, especially feelings are not measurable from data analysis. </p>\n<div class=\"Gallery-50\">\n![](./static/IMG_20180927_131449.jpg)\n![](./static/IMG_20180927_131426.jpg)\n<img src=\"\"></img>\n</div>\n<h2>Become a hybrid designer</h2>\n<p>Joining over 900 designers around the world, I’m also excited signing up two workshops, which were equally  fun and inspiring, including \"Game Design\" workshop hosted by Dori Adar (Hands on Games) and \"New Hybrid Designer\" facilitated by McKinsey Design. </p>\n<p>One reason why I chose these two workshops was to getting myself familiar with new aspect of the design, by introducing gamification to the design process and also talking about the business value of it.</p>\n<p>Becoming a hybrid designer has become more true in current days, in a sense that multi-discipline designers would be more communicative to make pragmatic and sensible design choice. </p>\n<h2>Takeaways</h2>\n<p>Making design happens takes more than a person or designers to be involved. Design values that we should uphold, mindset of management and clients are also equally important in the design process to make it work. </p>\n<p>After Intensive talks and workshops for two full-days. This trip has been rewarding for myself to take a look at my own approach and position. Taking a step back to see the bigger picture would make the journey of design come clear. </p>","excerpt":"It was a truly enlightening trip to Denmark, attending Design Matters 2018 in Copenhagen (Sep 26 -27, 2018). Not only did the talks and workshops given by design practitioners across the industry are illuminating, it had also made me realised to take…","frontmatter":{"path":"/journey-to-design-matters","title":"Journey to Design Matters","date":"September 29, 2018","tags":["Design","Conference","DesignMatters18"],"image":{"publicURL":"/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5.jpg","size":2848744,"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.3333333333333333,"src":"/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-03d5e.jpg","srcSet":"/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-192e7.jpg 285w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-849e0.jpg 570w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-03d5e.jpg 1140w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-12d50.jpg 1710w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-de0c2.jpg 2280w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-c9828.jpg 3420w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-61f08.jpg 4032w","srcWebp":"/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-7d805.webp","srcSetWebp":"/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-64c07.webp 285w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-a48d6.webp 570w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-7d805.webp 1140w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-828b8.webp 1710w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-e2747.webp 2280w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-80b8f.webp 3420w,\n/static/IMG_20180927_145719-d18245e0b48959cd43bc83af197c26c5-dc603.webp 4032w","sizes":"(max-width: 1140px) 100vw, 1140px"}}}}}},"pathContext":{}}

